import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, Button, Div, platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';

const osname = platform();

const Kalyga = props =>(
	<Panel id={props.id}>
	<PanelHeader 
	left={<HeaderButton onClick = {props.go} data-to = "home">
	{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
	</HeaderButton>}
	>
	  Калуга
	</PanelHeader>
	<Group>
	<Div>
	1.Площадь Победы
	<br/>
	<br/>
Композиция самой красивой и торжественной площади Калуги начала формироваться в 1966 г. Тогда здесь появилась 30-метровая гранитная стела, увенчанная бронзовой фигурой женщины, держащей в поднятых руках макет первого искусственного спутника земли и волнистую ленту, символизирующую реку Ока. Обелиск располагается в центре каменной чаши фонтана. Вокруг монумента размещены каменные плиты сложной формы с надписями, прославляющими подвиг советского народа – победителя фашизма и горельефами. В 1970 г. в центре аллеи, перед могилой Неизвестного солдата запылал Вечный огонь. Последним элементом мемориального ансамбля стал памятник маршалу Победы, установленный в 2015 г.
<br/>
<br/>
Остановка транспорта «Площадь Победы».
<br/>
<br/>
2.Каменный мост
<br/>
<br/>
Большинство калужан считают главной достопримечательностью именно этот мост – двойник романских виадуков, возводившихся во времена расцвета Римской империи. Сооружение имеет весьма интересную историю. Первую арочную конструкцию, соединившую берега речки Березуйки, которая давно пересохла, возвели на этом месте в 1777 г. К началу XX в. мост, проектировавшийся для пешеходов и гужевого транспорта, морально устарел. Его заменили железобетонным аналогом, простоявшим почти 90 лет. Однако время снова взяло свое. В 2009 г. сооружение частично обрушилось. Для его приведения в надлежащее состояние потребовалась серьезная реконструкция фундамента. Теперь калужский виадук вновь выглядит великолепно.
<br/>
<br/>
Адрес: ул. Пушкина. Остановка транспорта «Каменный мост».
<br/>
<br/>
3.Монумент к 600-летию Калуги
<br/>
<br/>
Памятник, сразу вошедший в число знаковых достопримечательностей Калуги, появился на въезде в историческую часть города в год, когда он праздновал свой 600-летний юбилей. Доминантой архитектурной композиции является компонентная стела, в центральной части которой размещен титановый барельеф первого космонавта Земли. У основания монумента установлена большая сфера, облицованная медным листом и символизирующая нашу планету. Особенно величественно памятник выглядит вечером, когда включаются прожекторы подсветки.
<br/>
<br/>
Адрес: ул. Гагарина. Остановка транспорта «Улица Комарова».

	</Div>
	</Group>
	</Panel>
);
Kalyga.propTypes = {
id: PropTypes.string.isRequired,
go: PropTypes.func.isRequired,
};

export default Kalyga;
